<template lang="pug">
  el-container(v-loading="loading")
    el-header
      h1.header-title housekeeper
      div.user-block(v-if="logged")
        el-popover(ref="logout"
                placement="bottom-start"
                width="50"
                v-model="logoutVisible")
          el-button(type="text" @click="logoutAndDismiss" style="width: 100%") Выйти
        el-button(v-popover="'logout'") вы вошли как:
          b  {{ userName }}
    el-main
      Login(v-if="!logged")
      div.content(v-else)
        el-tabs(@tab-click="changeRoute" :value="$route.name")
          el-tab-pane(label="Транзакции" name="book-keeping")
          el-tab-pane(label="Отчет" name="report")
          el-tab-pane(label="Настройки" name="settings")
        el-card
          el-row
            el-col(:sm="10")
              span Период:
              | &nbsp; &nbsp;
              el-date-picker(v-model="period" format="dd.MM.yyyy" @input="getTransactions" type="daterange")
            el-col(:sm="10" :offset="4")
              el-popover(placement="left-start" trigger="hover")
                el-table(:data="balanceBySources" border :show-header="false")
                  el-table-column(width="150" property="title" label="Источник" )
                  el-table-column(width="280" property="amount" label="Сумма")
                    template(slot-scope="scope" )
                      template(v-for="(currencyValue, currencyIndex) in scope.row.values")
                        span(:class="currencyValue.amount > 0 ? 'positive-amount' : 'negative-amount'") {{ currencyIndex > 0 ? ',' : '' }} {{ currencyValue.amount.toLocaleString() }} {{ currencyValue.currency}}
                p(style="line-height:2" slot="reference") Баланс:
                  span.just-amount {{ balance }}
        router-view.margin-top-10
</template>

<script>
import Login from '@/components/login'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'App',
  methods: {
    ...mapActions([
      'whoIAm',
      'logout',
      'getTransactions',
      'getCategories',
      'getMoneySources',
      'getBalances']),
    toCategories () {
      this.$router.push('categories')
    },
    logoutAndDismiss () {
      this.logoutVisible = false
      this.logout()
    },
    changeRoute (p) {
      this.$router.push({ name: p.name })
    },
    fetchData () {
      this.whoIAm()
        .then(() => {
          this.loading = false
          if (this.logged) {
            this.getBalances()
            this.getCategories()
              .then(() => {
                this.getTransactions(this.period)
                  .then(() => {
                    this.loading = false
                  })
              })
            this.getMoneySources()
          }
        }, () => { this.loading = false })
    }
  },
  computed: {
    ...mapGetters(['logged', 'userName', 'balance', 'balanceBySources'])
  },
  data () {
    const currentDate = new Date(Date.now())
    const dateFrom = new Date(Date.now() - 2700000000)
    return {
      logoutVisible: false,
      loading: true,
      period: [
        dateFrom,
        currentDate
      ]
    }
  },
  created () {
    this.fetchData()
  },
  watch: {
    logged () {
      this.loading = true
      this.fetchData()
    }
  },
  components: {
    Login
  }
}
</script>

<style lang="stylus">
  .margin-top-10
    margin-top 10px
  .margin-bottom-10
    margin-top 10px
  .just-amount
    font-weight bold
    padding 5px
  .positive-amount, .negative-amount
    font-weight bold
  .positive-amount
    color #0cbb55
  .negative-amount
    color #bb0c0c
  .content
    margin 0 auto
    max-width 1366px
  .user-block
    float right
    margin-top 10px
  .el-header
    background-color #2d2d2d
  .el-container
    background-color #f5f5f5
    min-height 790px
  .header-title
    padding-top 20px
    color #ffffff
    display inline-block
  .main-menu
    display inline-block
    float right
    padding-right 30px
    height 100%
    li
      color #fff
      display inline-block
      vertical-align middle
      padding 0 10px
      margin 0 10px
      line-height: 80px
      cursor pointer
  *
    font-family 'Exo 2', sans-serif
    margin 0
    padding 0
</style>



