const env = process.env.NODE_ENV
export default {
  transform (entity) {
    let s = JSON.stringify(entity)
    s = s.replace(/(\w)(\w*)/g, (g0, g1, g2) => {
      console.log(g0)
      console.log(g1)
      console.log(g2)
      return g1.toUpperCase() + g2
    })
    s = s.replace(/(:Null)/g, ':null')
    if (env === 'development') {
      console.log(env)
      console.log(entity)
      console.log(s)
    }
    console.log(s)
    entity = JSON.parse(s)
    return entity
  }
}



