
import HTTP from '@/utils/http-common'
import PascalCaseTransform from '@/utils/pascal-case-transform'

// endpoints
const URL_GET_TRANSACTIONS = '/bookkeeping/transactions'
const URL_TRANSACTION = '/bookkeeping/transactions/'
const URL_CATEGORIES = '/bookkeeping/categories/'
const URL_MONEYSOURCES = '/bookkeeping/moneysources/'
const URL_BALANCES = '/bookkeeping/balances/'

// mutation types
const CREATE_TRANSACTION = 'CREATE_TRANSACTION'
const UPDATE_TRANSACTION = 'UPDATE_TRANSACTION'
const DELETE_TRANSACTION = 'DELETE_TRANSACTION'
const SET_TRANSACTIONS = 'SET_TRANSACTIONS'

const SET_CATEGORIES = 'SET_CATEGORIES'
const CREATE_CATEGORY = 'CREATE_CATEGORY'
const UPDATE_CATEGORY = 'UPDATE_CATEGORY'
const DELETE_CATEGORY = 'DELETE_CATEGORY'
const SET_CURRENT_PERIOD = 'SET_CURRENT_PERIOD'

const SET_MONEYSOURCES = 'SET_MONEYSOURCES'
const CREATE_MONEYSOURCE = 'CREATE_MONEYSOURCE'
const UPDATE_MONEYSOURCE = 'UPDATE_MONEYSOURCE'
const DELETE_MONEYSOURCE = 'DELETE_MONEYSOURCE'

const SET_BALANCES = 'SET_BALANCES'

const state = {
  transactions: [],
  wallets: [],
  tags: [],
  categories: [],
  moneySources: [],
  balances: [],
  currentPeriod: null,
  currencies: [
    'грн',
    '$',
    '€'
  ]
}

const getters = {
  transactions: state => state.transactions,
  currencies: state => state.currencies,
  categories: state => state.categories,
  moneySources: state => state.moneySources,
  incomes: state => state.transactions.filter(t => t.amount > 0),
  expenses: state => state.transactions.filter(t => t.amount < 0),
  totalTransactionOf: (state, getters) => {
    return (type) => {
      let total = 0
      getters[type].forEach(element => {
        console.log(getters.category(element.categoryId))
        if (getters.category(element.categoryId).transit) {
          return
        }
        total += element.amount
      })
      return total
    }
  },
  totalIncome: (state, getters) => {
    return getters.totalTransactionOf('incomes')
  },
  totalExpense: (state, getters) => {
    return getters.totalTransactionOf('expenses')
  },
  totalBySourceOf: (state, getters) => {
    return (type) => {
      let totalBySources = []
      getters[type].forEach(element => {
        if (getters.category(element.categoryId).transit) {
          return
        }
        const id = element.moneySourceId || 0
        let totalBySource = totalBySources.find(f => f.id === id)
        if (!totalBySource) {
          totalBySource = { id, title: id ? getters.moneySource(id).title : 'Не определено', amount: 0 }
          totalBySources.push(totalBySource)
        }
        totalBySource.amount += element.amount
      })
      return totalBySources
    }
  },
  totalIncomeBySources: (state, getters) => {
    return getters.totalBySourceOf('incomes')
  },
  totalExpenseBySources: (state, getters) => {
    return getters.totalBySourceOf('expenses')
  },
  balance: (state, getters) => {
    let totalViaCurrency = {}

    getters.balanceBySources.forEach(element => {
      element.values.forEach(currencyValue => {
        if (!totalViaCurrency[currencyValue.currency]) {
          totalViaCurrency[currencyValue.currency] = 0
        }
        totalViaCurrency[currencyValue.currency] += currencyValue.amount
      })
    })
    let totalBalances = []
    for (let currency in totalViaCurrency) {
      const value = totalViaCurrency[currency]
      totalBalances.push(`${value.toFixed(2)} ${currency}`)
    }
    return totalBalances.length === 0 ? '0 грн' : totalBalances.join(', ')
  },
  balanceBySources: (state, getters) => {
    return state.balances
    /* const totalBalanceBySources = getters.totalIncomeBySources.slice(0, getters.totalIncomeBySources.length)
    const expenseBySources = getters.totalExpenseBySources
    console.log(totalBalanceBySources)
    expenseBySources.forEach(element => {
      let balanceBySource = totalBalanceBySources.find(x => x.id === element.id)
      if (!balanceBySource) {
        balanceBySource = { id: element.id, title: element.title, amount: 0 }
        totalBalanceBySources.push(balanceBySource)
      }
      balanceBySource.amount += element.amount
    })
    return totalBalanceBySources */
  },
  categoriesByType: (state, getters) => {
    return [
      {
        title: 'Расходы',
        categories: getters.getCategoryByType(1)
      },
      {
        title: 'Доходы',
        categories: getters.getCategoryByType(2)
      }
    ]
  },
  getCategoryByType: (state, getters) => {
    return type => getters.categories.filter(c => c.type === type)
  },
  tags: state => state.tags,
  category: (state) => {
    return id => state.categories.find(c => c.id === id)
  },
  moneySource: (state) => {
    return id => state.moneySources.find(c => c.id === id)
  }
}

const actions = {
  get ({ commit, state, rootGetters }, { url, mutation }) {
    return new Promise((resolve, reject) => {
      HTTP.get(url, { token: rootGetters.token })
        .then(response => {
          resolve()
          commit(mutation, { value: response.data })
        })
    })
  },
  create ({ commit, state, rootGetters, dispatch }, { url, mutation, entity, afterDispatch }) {
    HTTP.post(url, { token: rootGetters.token, request: PascalCaseTransform.transform(entity) })
      .then(response => {
        commit(mutation, { value: response.data })
        dispatch(afterDispatch)
      })
  },
  update ({ commit, state, rootGetters }, { url, mutation, entity }) {
    HTTP.patch(url, { request: entity, token: rootGetters.token })
      .then(response => {
        commit(mutation, { value: response.data })
      })
  },
  delete ({ commit, state, rootGetters }, { url, mutation, entity }) {
    return new Promise((resolve, reject) => {
      HTTP.delete(url, { token: rootGetters.token })
        .then(response => {
          if (response.data) {
            resolve()
            commit(mutation, { value: entity })
          } else {
            reject(response.data)
          }
        }, error => reject(error))
    })
  },
  getTransactions ({ commit, rootGetters }, period) {
    let periodValue = {}
    if (period && period.length === 2) {
      const dateFromParam = period[0]
      console.log(period)
      const dateFrom = dateFromParam
        ? (typeof dateFromParam === 'string' ? dateFromParam : new Date(dateFromParam.getFullYear(), dateFromParam.getMonth(), dateFromParam.getDate()).toDateString())
        : ''
      const dateToParam = period[1]
      const dateTo = dateToParam
        ? (typeof dateToParam === 'string' ? dateToParam : new Date(dateToParam.getFullYear(), dateToParam.getMonth(), dateToParam.getDate()).toDateString())
        : ''
      periodValue = {
        dateFrom,
        dateTo
      }
    }
    commit(SET_CURRENT_PERIOD, { period: Object.values(periodValue) })
    return new Promise((resolve, reject) => {
      HTTP.get(URL_GET_TRANSACTIONS, {
        token: rootGetters.token,
        request: {
          params: {
            ...periodValue
          }
        }
      })
        .then(response => {
          resolve()
          commit(SET_TRANSACTIONS, { transactions: response.data })
        })
    })
  },
  createTransaction ({ commit, state, rootGetters, dispatch }, transaction) {
    HTTP.post(URL_TRANSACTION, { request: PascalCaseTransform.transform(transaction), token: rootGetters.token })
      .then(response => {
        commit(CREATE_TRANSACTION, { transaction: response.data })
        dispatch('getTransactions', state.currentPeriod)
        dispatch('getBalances', state.currentPeriod)
      })
  },
  updateTransaction ({ commit, state, rootGetters, dispatch }, transaction) {
    HTTP.patch(URL_TRANSACTION + transaction.id, { request: transaction, token: rootGetters.token })
      .then(response => {
        commit(UPDATE_TRANSACTION, { transaction })
        dispatch('getBalances', state.currentPeriod)
      })
  },
  deleteTransaction ({ commit, state, rootGetters, dispatch }, transaction) {
    HTTP.delete(URL_TRANSACTION + transaction.id, { token: rootGetters.token })
      .then(response => {
        commit(DELETE_TRANSACTION, { transaction })
        dispatch('getBalances', state.currentPeriod)
      })
  },
  getCategories ({ dispatch }) {
    return dispatch('get', { url: URL_CATEGORIES, mutation: SET_CATEGORIES })
  },
  createCategory ({ dispatch }, category) {
    dispatch('create', { url: URL_CATEGORIES, mutation: CREATE_CATEGORY, entity: category, afterDispatch: 'getCategories' })
  },
  updateCategory ({ dispatch }, category) {
    dispatch('update', { url: URL_CATEGORIES + category.id, mutation: UPDATE_CATEGORY, entity: category })
  },
  deleteCategory ({ dispatch }, category) {
    return dispatch('delete', { url: URL_CATEGORIES + category.id, mutation: DELETE_CATEGORY, entity: category })
  },
  getMoneySources ({ dispatch }) {
    return dispatch('get', { url: URL_MONEYSOURCES, mutation: SET_MONEYSOURCES })
  },
  createMoneySource ({ dispatch }, entity) {
    dispatch('create', { url: URL_MONEYSOURCES, mutation: CREATE_MONEYSOURCE, entity: entity, afterDispatch: 'getMoneySources' })
  },
  updateMoneySource ({ dispatch }, entity) {
    dispatch('update', { url: URL_MONEYSOURCES + entity.id, mutation: UPDATE_MONEYSOURCE, entity: entity })
  },
  deleteMoneySource ({ dispatch }, entity) {
    return dispatch('delete', { url: URL_MONEYSOURCES + entity.id, mutation: DELETE_MONEYSOURCE, entity: entity })
  },
  getBalances ({ dispatch }) {
    return dispatch('get', { url: URL_BALANCES, mutation: SET_BALANCES })
  }
}

const mutations = {
  [CREATE_TRANSACTION] (state, { transaction }) {
    // state.transactions.push(transaction)
  },
  [UPDATE_TRANSACTION] (state, { transaction }) {
    let oldTransaction = state.transactions.find(t => t.id === transaction.id)
    Object.assign(oldTransaction, transaction)
  },
  [DELETE_TRANSACTION] (state, { transaction }) {
    let index = state.transactions.findIndex(t => t.id === transaction.id)
    state.transactions.splice(index, 1)
  },
  [SET_TRANSACTIONS] (state, { transactions }) {
    state.transactions = transactions
  },
  [SET_CATEGORIES] (state, { value }) {
    state.categories = value
  },
  [CREATE_CATEGORY] (state, { value }) {
    // state.transactions.push(transaction)
  },
  [UPDATE_CATEGORY] (state, { value }) {
    console.log(value)
    let oldCategory = state.categories.find(t => t.id === value.id)
    Object.assign(oldCategory, value)
  },
  [DELETE_CATEGORY] (state, { value }) {
    let index = state.categories.findIndex(t => t.id === value.id)
    state.categories.splice(index, 1)
  },
  [SET_CATEGORIES] (state, { value }) {
    state.categories = value
  },
  [SET_CURRENT_PERIOD] (state, { period }) {
    state.currentPeriod = period
  },
  [SET_MONEYSOURCES] (state, { value }) {
    state.moneySources = value
  },
  [CREATE_MONEYSOURCE] (state, { value }) {
    // state.transactions.push(transaction)
  },
  [UPDATE_MONEYSOURCE] (state, { value }) {
    let oldEntity = state.moneySources.find(t => t.id === value.id)
    Object.assign(oldEntity, value)
  },
  [DELETE_MONEYSOURCE] (state, { value }) {
    let index = state.moneySources.findIndex(t => t.id === value.id)
    state.moneySources.splice(index, 1)
  },
  [SET_BALANCES] (state, { value }) {
    value.forEach(balance => {
      balance.values.sort((first, second) => {
        if (first.currency === '$') {
          return 1
        }
        if (second.currency === 'грн') {
          return -1
        }
      })
    })
    state.balances = value
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



